<template>
  <div>
    <div>
      <h1>XSS</h1>
      <p>XSS又称CSS，全称Cross SiteScript，跨站脚本攻击，是Web程序中常见的漏洞，XSS属于被动式且用于客户端的攻击方式，所以容易被忽略其危害性。</p>
    </div>
    <div>
      <h2>XSS原理</h2>
      <p>攻击者向有XSS漏洞的网站中输入(传入)恶意的HTML代码，当其它用户浏览该网站时，这段HTML代码会自动执行，从而达到攻击的目的。如，盗取用户Cookie、破坏页面结构、重定向到其它网站等</p>
      <h2>xss 分类</h2>
      <h3>反射型XSS</h3>
      <p>非持久化 攻击者事先制作好攻击链接, 需要欺骗用户自己去点击链接才能触发XSS代码（服务器中没有这样的页面和内容），一般容易出现在搜索页面。</p>
      <h3>存储型XSS</h3>
      <p>持久化 代码是存储在服务器中的，如在个人信息或发表文章等地方，加入代码，如果没有过滤或过滤不严，那么这些代码将储存到服务器中，每当有用户访问该页面的时候都会触发代码执行，这种XSS非常危险，容易造成蠕虫，大量盗窃cookie（虽然还有种DOM型XSS，但是也还是包括在存储型XSS内）。</p>
      <h3>DOM型XSS</h3>
      <p>基于文档对象模型Document Objeet Model，DOM)的一种漏洞。DOM是一个与平台、编程语言无关的接口，它允许程序或脚本动态地访问和更新文档内容、结构和样式，处理后的结果能够成为显示页面的一部分。DOM中有很多对象，其中一些是用户可以操纵的，如uRI ，location，refelTer等。客户端的脚本程序可以通过DOM动态地检查和修改页面内容，它不依赖于提交数据到服务器端，而从客户端获得DOM中的数据在本地执行，如果DOM中的数据没有经过严格确认，就会产生DOM XSS漏洞。</p>
    </div>
    <div>
      <h2>XSS防御</h2>
      <h3>完善的过滤体系</h3>
      <p>永远不相信用户的输入。需要对用户的输入进行处理，只允许输入合法的值，其它值一概过滤掉</p>
      <h3>Html encode</h3>
      <p>某些情况下，我们不能对用户数据进行严格的过滤，那我们也需要对标签进行转换。</p>
    </div>
    <div></div>
  </div>
</template>

<script>
export default {
  name: 'XSS'
}
</script>

<style scoped>

</style>



